.footer {
  width: 100%;
  height: auto;
  position: fixed;
  padding: env(safe-area-inset-bottom);
  bottom: 0;
  left: 0;
  z-index: 9;
  transition: all ease 0.5s;
  -webkit-transition: all ease 0.5s;
  -moz-transition: all ease 0.5s;
  -ms-transition: all ease 0.5s;
  background: #ffffff;
  box-shadow: 0px -3px 10px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 0px -3px 10px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0px -3px 10px rgba(0, 0, 0, 0.1);
  -ms-box-shadow: 0px -3px 10px rgba(0, 0, 0, 0.1);
}

.footer .btn {
  width: 60px;
  height: 60px;
  line-height: 54px;
  padding: 0;
  text-align: center;
  display: block;
}

.footer .btn i {
  width: 22px;
  margin: 0 auto;
  display: inline-block;
  vertical-align: middle;
}

.footer .centerbutton {
  height: 60px;
  line-height: 54px;
  width: 60px;
  border-radius: 30px;
  margin: 0 15px;
  margin-top: -20px;

  .icon {
    font-size: increase-font-size($icon-size, 2px);
    width: auto;
  }
}

.footer {
  .icon {
    font-size: decrease-font-size($icon-size, 2px);
  }

  .centerbutton {
    height: 60px;
    width: 60px;
    border-radius: 30px;
    margin: 0 15px;
    margin-top: -20px;

    .icon {
      font-size: increase-font-size($icon-size, 2px);
      width: auto;
    }

    .badge-circle {
      position: absolute;
      bottom: -11px;
      font-size: 12px;
    }
  }

  &.chat-footer {
    box-shadow: unset;
    border-top: 1px solid lighten($bootstrap-secondary, 45);

    .centerbutton {
      height: 48px;
      width: 48px;
      border-radius: 30px;
      margin: 2.5px 15px;

      .icon {
        font-size: decrease-font-size($icon-size, 2px);
        width: auto;
      }

      .badge-circle {
        position: absolute;
        bottom: -11px;
        font-size: 12px;
      }
    }
  }
}
